

import React from 'react';
import { Link } from 'react-scroll';

const castMember = ({name, role}) => {
        const nameList = name.map((link) => 
        <Link to={link} offset={-100} smooth={true} duration={1000} >{link}</Link>            
        );
    return (
        <section>
        <p>{role}</p>
        <span>..............................................................................................................................................................</span>
        <p> 
            { nameList }
        </p>
        </section>
    )
};


export default castMember;